.SideDrawer {
    position:fixed;
    width:280px;
    max-width:80%;
    height:100%;
    left:0;
    top:0;
    background-color:white;
    z-index:200;
    padding:32px 16px;
    -webkit-box-sizing:border-box;
            box-sizing:border-box;
    -webkit-transition:-webkit-transform 0.3s ease-out;
    transition:-webkit-transform 0.3s ease-out;
    -o-transition:transform 0.3s ease-out;
    transition:transform 0.3s ease-out;
    transition:transform 0.3s ease-out, -webkit-transform 0.3s ease-out;
}
@media (min-width:500px) {
    .SideDrawer {
        display:none;
    }
}
.Open {-webkit-transform:translateX(0);-ms-transform:translateX(0);transform:translateX(0);}
.Close {-webkit-transform:translateX(-100%);-ms-transform:translateX(-100%);transform:translateX(-100%);}
.Logo {height:11%;margin-bottom:20px;}


