.Burger {
    width:100%;
    margin:auto;
    height:250px;
    text-align:center;
    font-weight:bold;
    font-size:1.2rem;overflow:auto;
}

@media (min-width:500px) and (min-height:401px) {
    .Burger {width:450px;height:400px;}
}
@media (min-width:1000px) and (min-height:700px) {
    .Burger {width:700px;height:600px;}
}


