.BuildControls {
    width:100%;background-color:#CF8F2E;display:-ms-flexbox;display:flex;-ms-flex-flow:column;flex-flow:column;-ms-flex-align:center;align-items:center;-webkit-box-shadow:0 2px 1px #ccc;box-shadow:0 2px 1px #ccc;margin:auto;
    padding:10px 0;
}
.OrderButton {
    background-color: #fff;/*#DAD735*/
    outline: none;
    cursor: pointer;
    border: 1px solid #966909;
    color: #966909;
    font-family: inherit;
    font-size: 1.2em;
    padding: 15px 30px;
    -webkit-box-shadow: 2px 2px 2px #966909;
            box-shadow: 2px 2px 2px #966909;
}

.OrderButton:hover, .OrderButton:active {
    background-color: #A0DB41;
    border: 1px solid #966909;
    color: #966909;
}

.OrderButton:disabled {
    background-color: #C7C6C6;
    cursor: not-allowed;
    border: 1px solid #ccc;
    color: #888888;
}

.OrderButton:not(:disabled) {
    -webkit-animation: enable 0.3s linear;
            animation: enable 0.3s linear;
}
@-webkit-keyframes enable {
    0% {-webkit-transform:scale(1);transform:scale(1);}
    60% {-webkit-transform:scale(1.5);transform:scale(1.5);}
    100% {-webkit-transform:scale(1);transform:scale(1);}
}
@keyframes enable {
    0% {-webkit-transform:scale(1);transform:scale(1);}
    60% {-webkit-transform:scale(1.5);transform:scale(1.5);}
    100% {-webkit-transform:scale(1);transform:scale(1);}
}


